import React, { useState } from "react";
import "./App.css"; /* optional for styling like the :hover pseudo-class */
import USAMap from "react-usa-map";

export default function App() {
  const usstate = [
    "AL",
    "AK",
    "AZ",
    "AR",
    "CA",
    "CO",
    "CT",
    "DE",
    "DC",
    "FL",
    "GA",
    "HI",
    "ID",
    "IL",
    "IN",
    "IA",
    "KS",
    "KY",
    "LA",
    "ME",
    "MD",
    "MA",
    "MI",
    "MN",
    "MS",
    "MO",
    "MT",
    "NE",
    "NV",
    "NH",
    "NJ",
    "NM",
    "NY",
    "NC",
    "ND",
    "OH",
    "OK",
    "OR",
    "PA",
    "RI",
    "SC",
    "SD",
    "TN",
    "TX",
    "UT",
    "VT",
    "VA",
    "WA",
    "WV",
    "WI",
    "WY",
  ];

  /* mandatory */
  const mapHandler = event => {
    alert(event.target.dataset.name);
  };

  const [state1, setState1] = useState("AL");
  const [state2, setState2] = useState("AL");
  const [state3, setState3] = useState("AL");

  /* optional customization of filling per state and calling custom callbacks per state */
  const statesCustomConfig = value => {
    let stateColor = {
      [value.split(",")[0]]: {
        fill: "navy",
      },
      [value.split(",")[1]]: {
        fill: "red",
      },
      [value.split(",")[2]]: {
        fill: "green",
      },
    };
    return stateColor;
  };

  let i = 0;
  return (
    <div className="App">
      {/* <USAMap customize={statesCustomConfig()} onClick={mapHandler} /> */}
      <USAMap
        customize={statesCustomConfig(state1 + "," + state2 + "," + state3)}
        onClick={mapHandler}
      />
      <div>
        <div>
          <select onChange={e => setState1(e.target.value)}>
            {usstate.map(data => (
              <option value={data} key={++i}>
                {data}
              </option>
            ))}
          </select>
        </div>
        <div>
          <select onChange={e => setState2(e.target.value)}>
            {usstate.map(data => (
              <option value={data} key={++i}>
                {data}
              </option>
            ))}
          </select>
        </div>
        <div>
          <select onChange={e => setState3(e.target.value)}>
            {usstate.map(data => (
              <option value={data} key={++i}>
                {data}
              </option>
            ))}
          </select>
        </div>
      </div>
    </div>
  );
}
